import React, { Suspense, lazy } from "react";
const Card = lazy(() => import('./Elements/Card'));
const CardSnapUp = lazy(() => import('../Elements/CardSnapUp'));

const Rows = ({ wall, itemData, completeDataObject, parentID, itemIndex, clickToShowPopUp, onClickToCTA, postSizeInPx, isOnsite, onClickPopUpSlider }) => {
    return <>
        <div className="tb_gt_post_small">
            {isOnsite && itemData[0] == 0 ?
                <div className={`tb_gt_post_wrapper tb_onsite_post__`}>
                    <Suspense fallback={<></>}> <CardSnapUp className="tb_gt_post_in" wall={wall} /> </Suspense>
                </div>
                :
                <Suspense fallback={<></>}>
                    <Card parentID={parentID} ownerId={wall.Wall.owner} wall={wall} itemData={completeDataObject[itemData[0]]} itemIndex={itemIndex} personalization={wall.Personalization} ThemeRule={wall.ThemeRule} clickToShowPopUp={clickToShowPopUp} wallID={wall.Wall.id} onClickToCTA={onClickToCTA} postSizeInPx={postSizeInPx} onClickPopUpSlider={onClickPopUpSlider} />
                </Suspense>
            }
            <Suspense fallback={<></>}>
                <Card parentID={parentID} ownerId={wall.Wall.owner} wall={wall} itemData={completeDataObject[itemData[1]]} itemIndex={itemIndex} personalization={wall.Personalization} ThemeRule={wall.ThemeRule} clickToShowPopUp={clickToShowPopUp} wallID={wall.Wall.id} onClickToCTA={onClickToCTA} postSizeInPx={postSizeInPx} onClickPopUpSlider={onClickPopUpSlider} />
            </Suspense>
        </div>
        <div className="tb_gt_post_large">
            <Suspense fallback={<></>}>
                <Card parentID={parentID} ownerId={wall.Wall.owner} wall={wall} itemData={completeDataObject[itemData[2]]} itemIndex={itemIndex} personalization={wall.Personalization} ThemeRule={wall.ThemeRule} clickToShowPopUp={clickToShowPopUp} wallID={wall.Wall.id} onClickToCTA={onClickToCTA} postSizeInPx={postSizeInPx} onClickPopUpSlider={onClickPopUpSlider} />
            </Suspense>
        </div>

    </>
}

export default Rows