import React, { PureComponent } from "react";
import { connect } from "react-redux";
import { Splide, SplideSlide, SplideTrack } from '@splidejs/react-splide';
import { Grid } from '@splidejs/splide-extension-grid';
import GalleryRow from "./GalleryRow";
import { getDataNextSteps } from '../../../actions/themeActions'
import '../scss/ThemesSplide.scss'
import './scss/GalleryTheme.scss'
import { isTagEmbed, isTagShop } from "../../../actions/api"
class GalleryTheme extends PureComponent {
  constructor(props) {
    super(props);
    this.state = { parent_id: '' }
    this.inputRefs = {};
  }
  componentDidMount() {
    setTimeout(() => {
      const classNameRole = document.querySelectorAll('.splide__slide');
      classNameRole.forEach((element, index) => {
        element.removeAttribute("role");
      });
    }, 100);
  }

  requestData = () => {
    const { appendData, wall, preRender, hasMoreData } = this.props;
    if (hasMoreData) {
      getDataNextSteps(
        wall.Wall.id,
        Math.floor(Date.now() / 1000),
        wall.ThemeRule.numberOfPosts,
        appendData.networkID,
        appendData.after,
        preRender,
        appendData.heightEvent
      );
    }
  };
  render() {
    const { postData, completeDataObject, wall, clickToShowPopUp, onClickToCTA, postSizeInPx, parentID, onClickPopUpSlider } = this.props;
    const inputName = parentID;
    const numberOfCoumn = wall.ThemeRule.numberOfCoumn;
    const mobileColumn = wall.ThemeRule.mobileColumn;
    const slidePost = wall.ThemeRule.slidePost;
    const slideDuration = wall.ThemeRule.slideDuration;
    const rowCount = wall.ThemeRule.multiRow;

    const settings = {

      grid: {
        rows: rowCount,
      },
      rewind: true,
      speed: 1000,
      interval: slidePost === 1 ? (parseInt(slideDuration) * 1000) : 5000,
      perPage: numberOfCoumn > 0 ? numberOfCoumn : isTagEmbed ? 3 : isTagShop ? 3 : 6,
      perMove: 1,
      gap: 0,
      autoplay: slidePost === 1 ? true : false,
      padding: 0,
      pagination: false,
      arrows: true,
      breakpoints: {
        560: {
          perPage: mobileColumn > 0 ? mobileColumn : 1,
        },
        767: {
          perPage: mobileColumn > 0 ? mobileColumn : 2,
        },
        991: {
          perPage: mobileColumn > 0 ? mobileColumn : 3,
        },
        1200: {
          perPage: numberOfCoumn > 0 ? numberOfCoumn : isTagShop ? 3 : 4,
        },
        1400: {
          perPage: numberOfCoumn > 0 ? numberOfCoumn : isTagShop ? 3 : 5,
        },
        1600: {
          perPage: numberOfCoumn > 0 ? numberOfCoumn : isTagEmbed ? 3 : isTagShop ? 3 : 6,
        }
      },
    };
    let newPostdata = [];
    const isOnsite = wall.UgcSettings.onsite_status ? true : false;
    for (let i = 0; i < postData.length;) {
      let gridLength = i == 0 && isOnsite ? 2 : 3;
      const merger = gridLength == 2 ? [0].concat(postData.slice(i, i + gridLength)) : postData.slice(i, i + gridLength)
      newPostdata.push(merger);
      i += gridLength;
    }
    newPostdata = newPostdata.length < 9 ? Array(10).fill(newPostdata).flat() : newPostdata;
    return (postData && postData.length > 2) ? <div className="tb_gt_post_container">
      <Splide hasTrack={false} className="tb_gt_post_slider" options={settings} extensions={{ Grid }} ref={(parentID) => this.inputRefs[inputName] = parentID}>
        <SplideTrack>
          {newPostdata.map((item, index) => {
            return (item && item.length == 3) ? <SplideSlide key={index} id={`tb_gt-${index}`} className={`tb_gt_post_row tb_${index}`} idd={parentID}>
              <GalleryRow parentID={parentID} ownerId={wall.Wall.owner} wall={wall} completeDataObject={completeDataObject} itemIndex={index} personalization={wall.Personalization} ThemeRule={wall.ThemeRule} clickToShowPopUp={clickToShowPopUp} wallID={wall.Wall.id} onClickToCTA={onClickToCTA} postSizeInPx={postSizeInPx} indexNo={index < 6 ? index : '0'} largeDiv={66.66} smallDiv={33.33} itemData={item} isOnsite={isOnsite} onClickPopUpSlider={onClickPopUpSlider} />
            </SplideSlide> : null
          })}
        </SplideTrack>
        <div className="splide__arrows splide__arrows--ltr tb_gt_arrow_wrapper_">
          <div className="tb_gt_arrow splide__arrow splide__arrow--prev tb_gt_arrow tb_gt_arrow_left__ tb__icon tb-arrow-left-alt" role="button" aria-label="Go to previous slide"><div></div></div>
          <div className="tb_gt_arrow splide__arrow splide__arrow--next  tb_gt_arrow_right__ tb__icon tb-arrow-right-alt" onClick={this.requestData} role="button" aria-label="Go to next slide"><div></div></div>
        </div>
      </Splide>
    </div> : null
  }
}

const mapStateToProps = state => {
  return {

  }
}

const mapDispatchToProps = dispatch => {
  return {
    getDataNextSteps: (wallID, timeStamp, postCount, networkId, after, postData, heightEvent, updateInStateNow) => dispatch(getDataNextSteps(wallID, timeStamp, postCount, networkId, after, postData, heightEvent, updateInStateNow)),
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(GalleryTheme);